@keyframes topMove {
  0% {
    bottom: 0px;
  }
  50% {
    bottom: 15px;
  }
  100% {
    bottom: 0px;
  }
}
@keyframes topMove2 {
  0% {
    bottom: 68px;
  }
  50% {
    bottom: 94px;
  }
  100% {
    bottom: 68px;
  }
}

.float_circle {
  position: absolute;
  width: 20px;
  bottom: 179px;
  left: 197px;
  animation: topCircle 5s linear infinite;
}
.float_circle2 {
  position: absolute;
  width: 12px;
  bottom: 134px;
  left: 192px;
  animation: topCircle2 5s linear infinite;
}
@keyframes topCircle {
  0% {
    bottom: 179px;
    left: 197px;
  }
  50% {
    bottom: 148px;
    left: 228px;
  }
  100% {
    bottom: 179px;
    left: 197px;
  }
}
@keyframes topCircle2 {
  0% {
    bottom: 134px;
    left: 192px;
  }
  50% {
    bottom: 134px;
    left: 230px;
  }
  100% {
    bottom: 134px;
    left: 192px;
  }
}
.float_shadow {
