  width: 100%;
}
.assets-header1 {
  padding: 0.5em 2em;
  border: solid;
  border-radius: 50px;
  border-width: 1px;
  cursor: pointer;
  border-color: #d7d7d7;
  font-weight: 500;
  background: none;
}
.assets-header2 {
  padding: 0.5em 2em;
  cursor: pointer;
  color: #ababab;
  background: none;
  border: none;
}
.search-input {
  width: 20%;
