@media screen and (max-width: 900px) {
  /* collateral assets section start */
  .assets-cont-head-area {
    align-items: flex-start;
    width: 100%;
    flex-direction: column;
    grid-row-gap: 20px;
  }
  .search-input {
    width: 100%;
  }
  .float_cube1 {
    position: absolute;
    width: 90px;
    bottom: 0;
    z-index: 2;
    animation: topMove 5s linear infinite;
  }
  .float_cube2 {
    position: absolute;
    /* width: 200px; */
    width: 90px;
    bottom: 68px;
    left: 45px;
    animation: topMove2 5s linear infinite;
  }
  .assets-cont-header-arae-btns {
    width: 100%;
    display: flex;
  }

  .assets-header1 {
    width: 50%;
  }
  .assets-header2 {
    width: 50%;
  }
  .assets-category-data {
    display: flex;
    font-size: 14px;
    align-items: center;
    justify-content: space-between;
  }

  td.assets-category-data::before {
    font-family: Inter, "Helvetica Neue", sans-serif;
    font-weight: 600;
    line-height: 1.5;
    font-size: 14px;
    color: var(--theme-ui-colors-text-muted, #708390);
    content: "Asset";
    display: block;
  }

  .assets-collateralize-button {
    width: 100%;
  }
  .assets-category-titles {
    display: none;
  }
  .assets-category-row {
    width: 100%;
    display: flex !important;
  }
  .assets-category-data1 {
    display: flex;
    font-size: 14px;
  }
  .assets-category-data1::before {
    font-family: Inter, "Helvetica Neue", sans-serif;
    font-weight: 600;
    line-height: 1.5;
    font-size: 14px;
    color: var(--theme-ui-colors-text-muted, #708390);
